body{
    margin-top: 10%;
    background: url("https://source.unsplash.com/1600x900/?nature,water") no-repeat;
overflow-x: hidden;
overflow-y: hidden;
font-family:'Times New Roman', Times, serif;
}
.box{
    background: linear-gradient(135deg, rgba(17, 129, 110, 0.486), rgba(41, 3, 3, 0.329));
margin: auto;
width: 400px;
height: 350px;
text-align: center;
color: white;
padding: 25px;
border-radius: 50px;
background: linear-gradient(top to bottom,rgb(94, 219, 11),rgb(15, 15, 15));
box-shadow: 
-8px -8px 12px 0 rgba(20, 20, 20, 0.863),
12px 12px 16px rgba(255, 255, 255, 0.842);

}
.box input{
    margin:20px auto;
    color:black;
    padding: 10px;
    border: 2px solid green;
    border-radius: 15px;
  
    box-shadow: 
    12px 12px 16px 0 rgba(0, 0, 0, 0.25),
    -8px -8px 12px 0 rgba(255, 255, 255, 0.3);
}
#temp{
  font-size: 28px;}
#temp_min,#temp_max{
  font-size: 24px;

}
.notfound{
  font-size: 26px;
  
}